$navbar-width: 290px;

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Segoe UI', Arial, sans-serif;
  line-height: 1.4;
  color: #444;
  background: #fff;
  height: 100vh;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
}

.inner {
  width: 100%;
  padding: 8rem 10px 4rem;
}

.navBar {
  position: relative;

  button {
    position: fixed;
    left: 40px;
    top: 40px;
    z-index: 10;
    cursor: pointer;
    background: transparent;
    border: none;
  }
}

.menuNav {
  overflow-y: scroll;
  list-style: none;
  position: fixed;
  top: 0;
  background: #283985;
  left: 0;
  bottom: 0;
  height: 100vh;
  width: 0;
  overflow: hidden;
  max-width: $navbar-width;
  z-index: 9;

  &.showMenu {
    width: 100%;
  }

  li:first-child {
    margin-top: 7rem;
  }
}

a {
  display: block;
  padding: 10px 40px;
  text-decoration: none;
  color: #efed18;
  text-transform: uppercase;
  font-weight: bold;
}

.active-link {
  background: #efed18;
  color: #283985;
}

.form-container {
  width: 100%;
  margin-bottom: 20px;
  display: flex;
  border-radius: calc(0.5 * 100px);
  box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.38);
  justify-content: space-evenly;
}

.input-text {
  font-size: 1rem;
  font-weight: 400;
  width: 85%;
  padding-right: 5px;
  padding-left: 10px;
  border-radius: calc(0.5 * 100px);
}

.input-text::placeholder {
  color: #000;
}

.input-submit {
  background: transparent;
  color: #5b5b5b;
  text-transform: capitalize;
  cursor: pointer;
  font-weight: 600;
  margin-right: 10px;
}

.input-text,
.input-submit {
  height: 45px;
  outline: none;
  border: none;
}

.input-submit svg {
  color: darkcyan;
  font-size: 20px;
  margin-top: 2px;
}

.todo-header {
  padding: 20px 0;
  line-height: 1.5em;

  &__title {
    font-size: 6rem;
    font-weight: 600;
    margin-bottom: 2rem;
    line-height: 1em;
    color: #696969;
    text-transform: upperCase;
    text-align: center;
  }
}

.about__content {
  padding: 8rem 40px;
  display: flex;

  &.about__content--pushed {
    padding-left: calc(#{$navbar-width} + 40px);
  }
}

.about__list {
  list-style: none;
  flex: 2;
  margin-right: 15px;

  a {
    color: #a70000;
    text-transform: capitalize;
    padding: 20px 0;
    font-weight: 600;

    &:hover {
      color: #000;
    }

    &.active-subLink {
      color: #3a5a81;
      position: relative;

      &:after {
        content: '';
        position: absolute;
        bottom: 0px;
        left: 0;
        width: 40%;
        height: 5px;
        background-color: #3a5a81;
      }
    }
  }
}

.main__content {
  flex: 7;

  h1 {
    margin-bottom: 15px;
    line-height: 30px;
    font-size: 30px;
  }

  p {
    line-height: 25px;
  }
}

@media (max-width: 850px) {
  .about__content--pushed.about__content--pushed {
    padding-left: unset;
  }

  .active-subLink::after {
    width: 85% !important;
  }
}
